// imported Modules =============================================
import { React, useState, useEffect } from "react";
import { routes, Navigate } from "react-router-dom";

import styled from "styled-components";
import { Input, Button, Menu, message } from "antd";
import { SearchOutlined } from "@ant-design/icons";

import axios from "axios";

// imported components ==========================================

// Main Component ===============================================
import DataStorage from "../DataStorage";
const ACCESS_MANAGER = DataStorage("ACCESS_MANAGER");
const ROUTE_MANAGER = DataStorage("ROUTE_MANAGER");

const FIND_OPTION = { route: 0, manual: 1 };
const PLACE_HOLDER = [
  { first: "Ïó∞Íµ¨ÏÜå Ïù¥Î¶ÑÏùÑ ÏïåÎ†§Ï£ºÏÑ∏Ïöî", second: "Ï∂úÏûÖ ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏïåÎ†§Ï£ºÏÑ∏Ïöî" },
  { first: "Ïó∞Íµ¨ÏÜå IDÎ•º ÏïåÎ†§Ï£ºÏÑ∏Ïöî", second: "Ïó∞Íµ¨ÏÜå Ï†ëÍ∑ºÏ†úÏñ¥Ïû•Ïπò Ï£ºÏÜåÎ•º ÏïåÎ†§Ï£ºÏÑ∏Ïöî" },
];

export default function LabFinder({ setIsLoading }) {
  const [isReady, setIsReady] = useState(false); // appÏúºÎ°ú ÎùºÏö∞ÌåÖÌï† Ï§ÄÎπÑÍ∞Ä ÎêòÏóàÎäîÏßÄ
  const [labAccessInfo, setLabAccessInfo] = useState(null); // labId, accessManagerUrl

  const [findOption, setFindOption] = useState(FIND_OPTION.route); // route, manual.

  const [firstInput, setFirstInput] = useState("");
  const [secondInput, setSecondInput] = useState("");

  // ‚ñº Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !!
  const [TEMP_DATALAB_AUTOACCESS, SET_TEMP_DATALAB_AUTOACCESS] = useState(false);
  // ‚ñ≤ Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! ‚ñ≤

  useEffect(() => {
    if (labAccessInfo) {
      setIsReady(true);
    }
  }, [labAccessInfo]);

  // ‚ñº Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! ‚ñº
  useEffect(() => {
    if (TEMP_DATALAB_AUTOACCESS === true && firstInput === "Îç∞Ïù¥ÌÑ∞Ïó∞Íµ¨ÏÜå") {
      onSubmit();
    }
  }, [firstInput]);
  // ‚ñ≤ Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! ‚ñ≤

  async function onSubmit(e) {
    switch (findOption) {
      case FIND_OPTION.route:
        setIsLoading(true);
        const postData = {
          taskType: "doRoute",
          data: {
            labName: firstInput,
            routePw: secondInput,
          },
        };
        const {
          data: { status, data },
        } = await axios.post(ROUTE_MANAGER, JSON.stringify(postData));

        if (status === "success") {
          setLabAccessInfo({
            labId: data,
            accessManagerUrl: ACCESS_MANAGER,
          });
          message.destroy();
          message.success("Ïó∞Íµ¨ÏÜåÎ°ú Îã¨Î†§Í∞ÄÍ≥†ÏûàÏñ¥Ïöî!");
        } else {
          console.log("ÏóêÎü¨!!ÏóêÎü¨!!!ÏóêÎü¨!!!");
          message.error({
            content: "Ïïó.. Ïó∞Íµ¨ÏÜåÎ•º Ï∞æÏßÄ Î™ªÌñàÏñ¥Ïöî...",
            duration: 0,
            key: "LabFinder_01",
            onClick: (e) => {
              message.destroy("LabFinder_01");
            },
          });
        }

        setIsLoading(false);
        break;

      case FIND_OPTION.manual:
        break;
      default:
        console.error("ÏòàÏÉÅÌïòÏßÄ Î™ªÌïú FIND_OPTIONÏûÖÎãàÎã§");
    }
  }

  return isReady ? (
    <routes>
      <Navigate
        to={{
          pathname: "/app",
          state: labAccessInfo,
        }}
      />
    </routes>
  ) : (
    <LabFinderWrap className="LabFinder">
      <SelectMenu
        onClick={(e) => {
          setFindOption(parseInt(e.key));
        }}
        selectedKeys={`${findOption}`}
        mode="horizontal"
      >
        <SelectItem key={`${FIND_OPTION.route}`}>Ïù¥Î¶ÑÏúºÎ°ú Ï∞æÍ∏∞</SelectItem>
        <SelectItem disabled key={`${FIND_OPTION.manual}`}>
          ÏßÅÏ†ë Ï∞æÍ∏∞
        </SelectItem>
      </SelectMenu>
      <FinderForm>
        <FinderInput
          placeholder={PLACE_HOLDER[findOption].first}
          size="large"
          value={firstInput}
          onChange={(e) => {
            setFirstInput(e.target.value);
          }}
          onPressEnter={onSubmit}
        />
        <FinderInputPw
          placeholder={PLACE_HOLDER[findOption].second}
          size="large"
          type="password"
          value={secondInput}
          onChange={(e) => {
            setSecondInput(e.target.value);
          }}
          onPressEnter={onSubmit}
        />
        <FinderButton icon={<SearchOutlined />} size="large" onClick={onSubmit}>
          Ïó∞Íµ¨ÏÜå Ï∞æÍ∏∞
        </FinderButton>

        {/* ‚ñº Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! ‚ñº  */}
        <FinderButton
          type="primary"
          size="large"
          style={{ marginTop: "16px" }}
          onClick={(e) => {
            setFirstInput("Îç∞Ïù¥ÌÑ∞Ïó∞Íµ¨ÏÜå");
            SET_TEMP_DATALAB_AUTOACCESS(true);
          }}
        >
          Îç∞Ïù¥ÌÑ∞Ïó∞Íµ¨ÏÜå ÏßÄÎèÑ Î≥¥Í∏∞(ÏûÑÏãú)
        </FinderButton>
        {/* ‚ñ≤ Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! Îç∞Ïù¥ÌÑ∞ Ïó∞Íµ¨ÏÜå Ï†ëÏÜçÏùÑ ÏúÑÌïú ÏûÑÏãú Í∏∞Îä• !! ‚ñ≤  */}
      </FinderForm>
    </LabFinderWrap>
  );
}

// style ========================================================
const LabFinderWrap = styled.div`
  display: flex;

  flex-direction: column;
  justify-content: center;
  align-items: center;

  margin-bottom: 32px;
  width: 80%;
`;

const SelectMenu = styled(Menu)`
  border: none;
  padding: 0px 8px;
  margin-bottom: 16px;
  background-color: #333333;
`;

const SelectItem = styled(Menu.Item)`
  color: white;
`;

const FinderForm = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;

  width: 350px;

  @media (max-width: 768px) {
    width: 90%;
  }
`;

const FinderInput = styled(Input)`
  width: 100%;
  margin-bottom: 16px;

  border-radius: 24px;

  border-color: white;
  &:hover {
    border-color: white;
  }
  &:focus {
    border-color: white;
  }
`;

const FinderInputPw = styled(Input.Password)`
  width: 100%;
  margin-bottom: 16px;

  border-radius: 24px;

  border-color: white;
  &:hover {
    border-color: white;
  }
  &:focus {
    border-color: white;
  }
`;

const FinderButton = styled(Button)`
  width: 100%;
  border-radius: 24px;
`;

